.cell{
    width: 100%;
    height: 100%;
    background: rgba(147, 112, 219, .9);
    border-radius: 4%;
    cursor: pointer;
    padding: 6px 10px;
    text-align: right;
}

.icon{
    fill: red;
    width: 60px;
}

@media (max-width: 400px) {
    .icon{
        width: 60%;
    }
}